<template>
  <div id="app">
    <!-- ÂÖàÁî®app-phoneÂåÖË£π‰∏âÈÉ®ÂàÜÔºåheaderÔºåÂàóË°®ÔºåÂ∞æÈÉ® -->
    <div class="app-phone">
      <div class="phone-header">
        <a class="cancel-cta" v-if="step===2 || step===3" @click="goHome">Cancel</a>
        <!-- Â§¥ÈÉ®ÊúâÂº†ÂõæÁâá -->
        <img src="./assets/vue_gram_logo_cp.png">
        <a class="next-cta" v-if="step===2" @click="step++">Next</a>
        <a class="next-cta" v-if="step===3" @click="share">Share</a>
      </div>

      <app-phonebody
        :posts="Postsdata"
        :step="step"
        :image="image"
        :filters="filters"
        v-model="caption"
      ></app-phonebody>
      <div class="phone-footer">
        <div class="home-cta">
          <!-- ÂºïÂÖ•ÂõæÊ†áÂ∫ì -->
          <i class="fas fa-home fa-lg" @click="goHome"></i>
        </div>
        <!-- ÁÇπÂáªÊñá‰ª∂‰∏ä‰º†ÔºåÁî®input -->
        <div class="upload-cta">
          <!-- Âú®Ê†∑ÂºèÈáåËÆæÁΩÆnameÁ≠â‰∫éfileÂ∞±ÈöêËóè -->
          <!-- ‰∏ä‰º†Êñá‰ª∂Â∞±‰ºöËß¶Âèëchange‰∫ã‰ª∂ -->
          <input
            type="file"
            id="file"
            name="file"
            @change="uploadImg"
            accept="image/jpg, image/png, image/jpeg, image/gif"
            :disabled="step !== 1"
          >
          <!-- ÊääÂõæÊ†áÊîæËøõlabelÔºåÁÇπÂáªÂõæÊ†áÂ∞±‰ºöÁõ∏ÂΩì‰∫éÁÇπÂáªinputÊ°Ü -->
          <label for="file">
            <i class="far fa-plus-square fa-lg"></i>
          </label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import phoneBody from "./components/phonebody.vue";

// ÊåâËØ¥Â∫îËØ•ÂêéÂè∞ÁªôÔºåËøôÈáåÊ®°ÊãüÂêéÂè∞ËøîÂõûÁöÑÊï∞ÊçÆÔºåÁî®jsÊñá‰ª∂‰ª£Êõø
import Postsdata from "./data/posts.js";

// ÂºïÂÖ•Êª§ÈïúÁ±ªÂêç
import filters from "./data/filters.js";
export default {
  name: "app",
  data() {
    return {
      Postsdata,
      step: 1,
      image: "",
      filters,
      caption: ""
    };
  },
  components: {
    "app-phonebody": phoneBody
  },
  methods: {
    uploadImg(event) {
      // Áî®ÂèòÈáèfilesÂ∞Ü‰∏ä‰º†ÁöÑÊñá‰ª∂Â≠ò‰∏Ä‰∏ã
      const files = event.target.files;
      // !files.lengthÁõ∏ÂΩì‰∫éfiles.length==0ÔºåÊ≤°‰∏ä‰º†
      // console.log(event)
      if (!files.length) {
        return;
      }

      // Ê∑ªÂä†ÂõæÁâáÂπ∂‰∏ä‰º†
      const reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onload = e => {
        // console.log(e)
        this.image = e.target.result;
        // ‰∏ä‰º†ÊàêÂäüÂêéÔºå‰ºöË∑≥Âà∞Êª§ÈïúÈÇ£‰∏™È°µÈù¢ÔºåÊâÄ‰ª•phonebodyÂèò‰∫ÜÔºåÊÉ≥Âà∞Áî®v-ifËøõË°ådomËäÇÁÇπÊõøÊç¢
        this.step = 2;
      };

      // ÂºïÁî®Â∫ìÔºåÊ∑ªÂä†Êª§Èïú
    },
    goHome() {
      (this.step = 1), (this.image = "");
    },
    share() {
      const post = {
        username: "MOSS",
        userImage:
          "https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg",
        postImage:this.image,
        likes: 0,
        hasBeenLiked: false,
        caption: this.caption,
        filter: this.filter
      }
      this.Postsdata.unshift(post)
      this.goHome()
    }
  }
};
</script>

<style lang="scss" src='./styles/app.scss'>
