// ‰∏Ä‰∏™vuegram-postÂ∞±ÊòØ‰∏ÄÊù°Âä®ÊÄÅÔºåÊÉ≥ÁùÄÊ†πÊçÆÊï∞ÊçÆÂä®ÊÄÅÂä†ËΩΩÔºåÊâÄ‰ª•Êää‰∏Ä‰∏™Âä®ÊÄÅÁã¨Á´ãÊàêÁªÑ‰ª∂,Â∞ÜÊâÄÊúâpost[0]Êç¢Êàêpost
<template>
  <div class="vuegram-post">
    <div class="header level">
      <!-- Áî®bulmaÂ∫ìÈáåÁöÑÂ∏ÉÂ±ÄÔºålevel -->
      <div class="level-left">
        <!-- Â§¥ÂÉè -->
        <figure class="image is-32x32">
          <!-- ÊåâËØ¥Âä®ÊÄÅ‰ªéÂêéÂè∞Âä†ËΩΩposts ÔºåËÆ∞ÂæóË¶Åv-bind-->
          <img :src="post.userImage">
        </figure>
        <span class="username">{{ post.username}}</span>
      </div>
    </div>
    <!-- Âíåheader‰∏ç‰∏ÄÊ†∑ÔºåËøôÈáåÁõ¥Êé•ÁªôÊàêËÉåÊôØÂõæÁâáÔºå‰πüË¶ÅÁªëÂÆöstyleÔºå‰∏çËÉΩÂøò‰∫Ü{} -->
    <!-- Êª§ÈïúÁî®ÁöÑÊòØflitersÂ±ûÊÄß -->
    <!-- ÂèåÂáªÂõæÁâáËÉΩÁÇπËµûÊàñÂèñÊ∂àËµûÔºåÁªëÂÆö‰∫ã‰ª∂ -->
    <div
      class="image-container"
      :style="{backgroundImage:'url('+ post.postImage +')'}"
      :class="post.filter"
      @dblclick='clicklike'
    ></div>
    <div class="content">
      <div class="heart">
          <!-- ÂçïÂáªÂøÉÁÇπËµûÊàñÂèñÊ∂àËµûÔºåÁªëÂÆö‰∫ã‰ª∂ -->
        <!-- fasÊòØÂÆûÂøÉÔºåfarÊòØÁ©∫ÂøÉÔºåÊù•ÊîπÂèòÁ©∫ÂøÉÊàñÂÆûÂøÉÔºåÊîπÂèòËµû -->
        <i class="far fa-heart" 
        @click="clicklike"
        :class="{fas:this.post.hasBeenLiked}"></i>
      </div>
      <p class="likes">{{ post.likes }} likes</p>
      <p class="caption">
        <span>{{ post.username}}</span>
        {{ post.caption}}
      </p>
    </div>
  </div>
</template>
<script>
export default {
    props:{
        post:Object
    },
    methods:{
        clicklike(){
            // ÊâìÂç∞‰∏Ä‰∏ãthis.post,ÂèØ‰ª•ÁúãÂá∫ÈáåÈù¢Êúâ‰∏™Â≠óÊÆµËÉΩÂ§üÂà§Êñ≠ÂΩìÂâçÂõæÊ†áÁä∂ÊÄÅhasBeenLikedÊòØtrue false
            // console.log(this.post)
            // Â¶ÇÊûúÂΩìÂâçÁä∂ÊÄÅÊòØtrueÈÇ£‰πàlikesÊï∞ÈáèË¶ÅÂáè‰∏Ä
            this.post.hasBeenLiked ? this.post.likes-- : this.post.likes++
            // Ë¶ÅÂèñÂèçÔºåÂê¶Âàô‰∏ÄÁõ¥Âä†
            this.post.hasBeenLiked = !this.post.hasBeenLiked
        }
    }
};
</script>
<style lang="scss" src='../styles/vuegram-post.scss'>
</style>

